___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  background-color: #bf1722;
  color: white;
  z-index: 100;
}
.skip-link:focus {
  top: 0;
}

/* loading */
.hidden {
  display: none;
}

.loading {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.5em;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 1em;
  border-radius: 10px;
}

/*
    AppBar
  */

.app-bar {
  padding: 8px 16px;
  background-color: #070f2b;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  align-items: center;
}

.app-bar__brand .logo span {
  font-family: "EB Garamond", serif;
  font-size: 2rem;
}

.app-bar__brand .logo {
  font-size: 2rem;
  font-weight: bold;
  text-decoration: none;
  font-family: "poppins", serif;
  color: #fff;
  user-select: none;
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__menu button {
  width: 44px;
  height: 44px;
  border-radius: 10px;
  background-color: #070f2b;
  color: white;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: #070f2b;
  font-size: 1.5rem;
}

.app-bar .app-bar__navigation a:hover {
  color: #007bff;
  font-weight: bold;
}

.app-bar .app-bar__navigation.open {
  left: 0;
  margin-top: 10px;
}

.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: white;
  padding: 8px;
  margin-bottom: 20px;
  width: 100%;
}

/*
    Main Content
  */

main {
  padding: 0;
  flex: 1;
}

.restaurants {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  padding: 20px;
}

.restaurant-item {
  border: 1px solid #ddd;
  border-radius: 7px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #e9f0ef;
  padding: 0;
}

.restaurant-item__content p {
  font-size: 1rem;
  color: #666;
  padding: 0 10px;
}

.restaurant-item__content a {
  color: #007bff;
  text-decoration: none;
  padding: 10px 44px 44px 10px;
}

.restaurant-item__content a:hover {
  text-decoration: underline;
}

.restaurant-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.content .content__heading {
  font-weight: bold;
  text-align: center;
  padding-top: 20px;
}

/* Hero section */
.hero {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  padding: 0;
  margin: 0;
}
.hero .kata {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 20px;
  border-radius: 20px;
}
.hero h1 {
  font-size: 1.4rem;
}
.hero p {
  font-size: 1.2rem;
}

/*
    detail
  */
.restaurant__name {
  text-align: center;
  padding: 10px;
}

.restaurant__descrition {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
}
.restaurant__poster {
  width: 80%;
  padding-left: 8vw;
}

.restaurant__info {
  padding: 7vw;
}

.restaurant__descriptionkedua {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  background-color: #e9f0ef;
}

.restaurant__menus {
  padding-left: 9vw;
}

.restaurant__descriptionkedua h3 {
  font-size: 1.5rem;
  padding-top: 4vw;
  padding-bottom: 0;
  text-align: center;
}

.restaurant__info button {
  background-color: #3498db;
  color: white;
  min-width: 44px;
  min-height: 44px;
  padding: 12px 12px;
  margin-top: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.restaurant__info button:hover {
  background-color: #2980b9;
}

.restaurant__menu {
  display: flex;
  padding: 7vw;
}

.restaurant__menu .food {
  padding-right: 70px;
  padding-left: 15px;
}

.restaurant__reviews {
  background-color: #344c64;
  color: #fff;
  padding-bottom: 10px;
}
.reviews__item {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 7vw;
  grid-gap: 20px;
}

.review {
  padding: 1vh;
  background-color: #e9f0ef;
  color: #000;
  border: 1px solid #ddd;
  border-radius: 7px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.restaurant__reviews h3 {
  text-align: center;
}

/*
    Footer
  */

footer {
  background-color: #070f2b;
  padding-top: 10px;
  padding-bottom: 10px;
  color: white;
  font-size: 1rem;
  width: 100%;
  text-align: center;
  font-family: "poppins", serif;
}

footer span {
  color: white;
  font-size: 1rem;
  font-family: "EB Garamond", serif;
}
